.content_3 {
  margin-top: 5px;
  background: #fff;
  padding: 30px 30px 60px;
  border-radius: 5px;
  box-shadow: 0 0 3px #eee;
  min-height: calc(100vh - 336px);
}
.content_3 .paper-right-bottom {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.content_3 .announcement-list {
  padding: 22px 30px;
  background: #F9F9F9;
  margin: 8px 0;
  display: flex;
  align-items: center;
}
.content_3 .announcement-list .ant-btn {
  border: 1px solid #3E78ED;
  color: #3E78ED;
  margin-left: 20px;
  width: 75px;
  height: 29px;
}
.content_3 .announcement-list .ant-btn:first-child {
  background: #3E78ED;
  color: #fff;
}
.content_3 .announcement-list .ant-col:nth-of-type(1) {
  display: flex;
  align-items: center;
}
.content_3 .announcement-list .ant-col:nth-of-type(1) img {
  width: 41px;
  height: 41px;
  margin-right: 22px;
}
.content_3 .announcement-list .ant-col:nth-of-type(1) {
  display: flex;
  align-items: center;
}
.content_3 .announcement-list .ant-col:nth-of-type(1) img {
  width: 41px;
  height: 41px;
  margin-right: 22px;
}
.content_3 .announcement-list .ant-col:nth-of-type(2) {
  display: flex;
  justify-content: flex-end;
}
.content_3 .announcement-list h2 {
  font-size: 16px;
  width: 750px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  vertical-align: middle;
  color: #2A283D;
  opacity: 0.5;
}
.content_3 .announcement-list p {
  color: #999;
  margin: 0;
}
.content_3 .announcement-list p span {
  margin-right: 10px;
  color: #2A283D;
  opacity: 0.5;
}
.content_3 .add-announcement {
  display: flex;
  position: absolute;
  right: 10px;
  top: -56px;
}
.content_3 .add-announcement .ant-btn {
  background: #3E78ED;
  color: #fff;
  border: none;
  height: 29px;
}
